* {box-sizing:border-box}

body, html {
	position: relative;
	background-color:#fff;
	font-family: 'Oswald', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
	font-size: 14px;
	margin:0;
	padding:0;
	color: #000;
	letter-spacing: initial;
	font-weight: 300;

}

.list-inline{
    margin: 0px;
    padding-left: 0;
	list-style: none;
}
.list-inline>li {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
}
.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.boxed {
    position: relative;
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}
.text-center{
text-align: center;
}